.button {
  background: $color-white;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  height: 100%;
  transition-duration: 0.3s;

  &__add {
    font-weight: 600;
    font-size: 14px;
    line-height: 21px;

    text-align: center;

    color: $color-white;
    width: 176px;
    background-color: $color-primary;

    &:hover {
      color: $color-white;
      background: $color-primary;
      box-shadow: -1px 3px 13px -1px rgba(23, 32, 49, 0.26);
    }

    &--selected {
      color: $color-green;
      background-color: transparent;
      border: 1px solid $gray-icons-placeholders;
    }
  }

  &__fav {
    border: 1px solid $gray-icons-placeholders;

    width: 64px;

    &:hover {
      border: 1px solid $color-primary;
    }
  }

  &__cart {
    border: 1px solid $gray-icons-placeholders;
    width: 64px;
    height: 64px;
    position: relative;

    &--image {
      width: 16px;
      height: 16px;
    }

    // &::after {
    //   content: attr(data-counter);

    //   position: absolute;

    //   width: 14px;
    //   height: 14px;

    //   bottom: 50%;
    //   left: 50%;

    //   border-radius: 50%;

    //   background-color: $color-red;

    //   color: $color-white;

    //   font-size: 10px;
    //   line-height: 16px;

    //   border: 2px solid $color-white;
    // }
  }
}
